import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import "./css/test_page.css";
const Testpage = () => {
  return (
    <div className="testpagescreen">
      <section className="banner_section">
        <Container fluid>
          <Row>
            <Col lg={12}>
              <div className="banner_wrapper">
                <h1>
                  Exploring the World of Entertainment In a Adventurious
                  LifeCycle
                </h1>
                <p>
                  Entertainment is a vital part of our lives, offering a diverse
                  array of experiences that bring joy, relaxation, and
                  excitement. From movies and music to video games and live
                  performances, the world of entertainment provides endless
                  opportunities to explore new stories, discover talented
                  artists, and connect with others. Whether you're enjoying a
                  quiet night with a good book or cheering at a sports event,
                  entertainment enriches our lives in countless ways.
                </p>
                <p>
                  The evolution of entertainment has been remarkable, with
                  advancements in technology transforming the way we consume and
                  enjoy content. Streaming services have made it possible to
                  access movies, TV shows, and music from around the globe at
                  the click of a button. Virtual reality and augmented reality
                  are creating immersive experiences that transport audiences to
                  fantastical worlds. As the boundaries between different forms
                  of entertainment continue to blur, the possibilities for
                  creative expression and audience engagement are limitless,
                  making it an exciting time to be both a creator and a
                  consumer.
                </p>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
      <article className="entertainment_drama_section">
        <Container fluid>
          <Row>
            <Col lg={12}>
              <div className="entertainment_drama">
                <h2>Dark Side Of Entertainment</h2>
                <p>
                  Back in the stone ages of television, when Doordarshan was the
                  only channel available, there were no afternoon programs to
                  entertain viewers. You had to patiently wait until the evening
                  for something exciting, like the beloved Chitrahaar. Switching
                  channels wasn’t even an option back then. Fast forward to
                  today, and we have transitioned from a single channel to an
                  overwhelming array of options on social media, numbering in
                  the millions. Changing channels has become as effortless as
                  blinking. It takes lesser time to change a channel than it
                  takes a long weekend to pass by.
                </p>
                <p>
                  When it comes to content, many videos found on platforms like
                  TikTok push the boundaries of human foolishness to new
                  heights. Films like ‘Gunda’ achieved cult status due to their
                  unintentional hilarity. Short cringe videos are like that, but
                  on steroids. While some may find enjoyment in these videos
                  precisely because of their low quality, others exceed even the
                  lowest expectations, reaching levels of stupidity that defy
                  description. To label them as merely “stupid” would be a
                  generous understatement.
                </p>
                <p>
                  However, the current landscape of social media suggests that
                  some individuals may have immersed themselves too deeply in
                  portraying exaggerated personas, blurring the lines between
                  online foolishness and real-life authenticity. The younger
                  generation spends extensive hours on social media, either
                  creating or consuming cringe-inducing content. This represents
                  a considerable opportunity cost for these individuals and for
                  society as a whole, as the time spent could have been
                  allocated to more meaningful pursuits.
                </p>
                <p>
                  As we navigate this evolving landscape, it is essential to
                  strike a balance between entertainment and introspection,
                  ensuring that we do get our dose of laughter without the
                  degradation of intellectual discourse.
                </p>
              </div>
            </Col>
          </Row>
        </Container>
      </article>

      <Container>
        <Row>
          <Col lg={12}>
            <div className="about_album">
              <h2>Albums that shaped a generation and far beyond</h2>
              <p>
                This Rolling Stones album was made in the midst the psychedelic
                rock movement. Yes, it is one of the greatest rock albums ever
                made. Beggars Banquet is a priceless psychedelic gem from that
                era. It demonstrates the band’s strength handling deftly a
                genre, which most bands of the time clumsily passed through
                embarrassed. Most rock bands which ventured into the psychedelia
                would not want to revisit their works, Beatles included. The
                album Beggars Banquet is the work of this amazing band of
                Londoners singing about their aspirations, loves, lust, sadness
                and loneliness of living and working in working class London.
              </p>
              <p>
                The music traverses and strides effortlessly the genres of
                psychedelia, rock, blues, soul, gospel and Indian music with
                luminous lyrics. The lyrics are rooted in working class London,
                so unlike the later day Stones’ head on hedonism of ‘drugs sex
                and rock and roll’. All the songs on Beggars Banquet were
                written by Mick Jagger and Keith Richards. Brian Jones with his
                drug addiction was by now a receding force, even though his
                musical contributions are anything but spectacular.
              </p>
              <p>
                The opening act is ‘Sympathy for the Devil’, a psychedelic rock
                classic. Mick Jagger singing a first person account and from the
                perspective of the devil is a treat. Jagger singing and
                screaming the devilishly clever lyrics can be counted his very
                best.
              </p>
            </div>
          </Col>
        </Row>
      </Container>
      <section className="sports_entertainment_section">
        <Container>
          <Row>
            <Col lg={12}>
              <div className="sports_entertainment">
                <h2>The meaning of life Lies Entertainment</h2>
                <p>
                  The meaning of life lies where you are not scared of anything.
                  You can see yourself smiling at those attempt of life where
                  nothing is going perfect but for a small period of time that
                  you are happy holding someone’s hand
                </p>
                <div className="sports_inner_content">
                  <Row>
                    <Col lg={6}>
                      <div>
                        <p>
                          An “official” sports blog will typically exist for
                          competitive sports. These are typically the most
                          educational. But the flavor of life is in the variety.
                          Additional 1000s of Sports blogs for amateur athletes
                          and those who participate in various sports purely for
                          their social and physical benefits.
                        </p>
                      </div>
                    </Col>
                    <Col lg={6}>
                      <div>
                        <p>
                          If you plan to create a website for your sports blog
                          project, you might need these sports blog designs that
                          will motivate you to build an online presence
                          effectively. Consequently, numerous bloggers create
                          general sites covering various sports and
                          sport-specific blogs. This way, bloggers can easily
                          present views and personal opinions while turning
                          sports passions into a full-time career.
                        </p>
                      </div>
                    </Col>
                  </Row>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
      </section>
    </div>
  );
};

export default Testpage;
